import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
    name: 'zoneStatus'
})
export class ZoneStatusPipe implements PipeTransform {

    transform(value: unknown, ...args: unknown[]): unknown {
        let result = '';
        if (value) {
            switch (value) {
                case 'READY':
                    result = '';
                    break;
                case 'INITIALIZING':
                    result = '';
                    break;
                case 'UPLOADIMAGEERROR':
                    result = '';
                    break;
            }
        }
        return result;
    }
}

